import React, { useState, useEffect, useCallback } from 'react';
import {
    Container,
    Paper,
    Typography,
    Box,
    IconButton,
    Button,
    Divider,
    Grid,
    Card,
    CardContent,
    TextField,
    Alert,
    Collapse,
    Fade,
} from '@mui/material';
import {
    Add as AddIcon,
    Remove as RemoveIcon,
    Delete as DeleteIcon,
    ShoppingCart as CartIcon,
    LocalShipping as ShippingIcon,
} from '@mui/icons-material';
import { useNavigate } from 'react-router-dom';
import debounce from 'lodash/debounce';
import { createAuthAxios } from '../../utils/auth';

const CART_SERVICE_URL = 'https://ordermanagersystem.onrender.com';

const Cart = () => {
    const navigate = useNavigate();
    const [cartItems, setCartItems] = useState([]);
    const [showAlert, setShowAlert] = useState(false);
    const [alertMessage, setAlertMessage] = useState('');
    const [alertSeverity, setAlertSeverity] = useState('success');
    const [pendingUpdates, setPendingUpdates] = useState({});

    //  useMemo  authAxios 
    const authAxios = React.useMemo(() => createAuthAxios(navigate), [navigate]);

    // 
    const showSuccessMessage = useCallback((message) => {
        setShowAlert(true);
        setAlertMessage(message);
        setAlertSeverity('success');
        setTimeout(() => setShowAlert(false), 3000);
    }, []);

    // 
    const showErrorMessage = useCallback((message) => {
        setShowAlert(true);
        setAlertMessage(message);
        setAlertSeverity('error');
        setTimeout(() => setShowAlert(false), 3000);
    }, []);

    // 
    const handleError = useCallback((error) => {
        //  401 axios 
        showErrorMessage(error.response?.data?.error || '');
    }, [showErrorMessage]);



    useEffect(() => {
        let isMounted = true;
        const controller = new AbortController();

        const getCartData = async () => {
            try {
                console.log('...');
                const response = await authAxios.get(`${CART_SERVICE_URL}/api/v1/cart/`, {
                    signal: controller.signal
                });

                if (!isMounted) return;

                const data = response.data;
                console.log(':', data);

                if (data.items) {
                    setCartItems(data.items.map(item => ({
                        id: item.ProductID,
                        name: item.Name,
                        price: item.Price,
                        quantity: item.Quantity,
                        image: item.Image || "https://via.placeholder.com/150",
                        stock: item.StockCount
                    })));
                }
            } catch (error) {
                if (error.name === 'AbortError' || !isMounted) return;

                console.error(':', error);
                showErrorMessage(error.message);
            }
        };

        getCartData();

        return () => {
            isMounted = false;
            controller.abort();
        };
    }, [authAxios, showErrorMessage]);

    //  debounce 
    const debouncedUpdateQuantity = useCallback((id, quantity) => {
        //  debounce  useCallback 
        const updateQuantity = debounce(async (itemId, itemQuantity) => {
            try {
                await authAxios.put(`${CART_SERVICE_URL}/api/v1/cart/items`, {
                    ProductID: itemId,
                    Quantity: itemQuantity
                });

                // 
                setPendingUpdates(prev => {
                    const newUpdates = { ...prev };
                    delete newUpdates[itemId];
                    return newUpdates;
                });

                showSuccessMessage('');
            } catch (error) {
                console.error(':', error);
                handleError(error);
            }
        }, 1000);

        updateQuantity(id, quantity);
    }, [authAxios, showSuccessMessage, handleError]);

    // 
    const handleQuantityChange = (id, newQuantity) => {
        // 
        const quantity = Math.max(1, Math.min(99, Number(newQuantity) || 1));

        // 
        setCartItems(prev =>
            prev.map(item =>
                item.id === id ? { ...item, quantity } : item
            )
        );

        // 
        setPendingUpdates(prev => ({
            ...prev,
            [id]: quantity
        }));

        // 
        debouncedUpdateQuantity(id, quantity);
    };

    // 
    const removeItem = async (id) => {
        try {
            await authAxios.delete(`${CART_SERVICE_URL}/api/v1/cart/items/${id}`);
            setCartItems(items => items.filter(item => item.id !== id));
            showSuccessMessage('');
        } catch (error) {
            console.error(':', error);
            handleError(error);
        }
    };

    // 
    const handleClearCart = async () => {
        try {
            await authAxios.delete(`${CART_SERVICE_URL}/api/v1/cart/`);
            setCartItems([]);
            showSuccessMessage('');
        } catch (error) {
            console.error(':', error);
            handleError(error);
        }
    };

    // 
    const total = cartItems.reduce((sum, item) => sum + (item.price * item.quantity), 0);

    //  2000 
    const shippingFee = total >= 2000 ? 0 : 100;

    // 
    const handleCheckout = () => {
        navigate('/checkout');
    };

    // 
    const updateQuantityImmediately = useCallback(async (id, quantity) => {
        try {
            await authAxios.put(`${CART_SERVICE_URL}/api/v1/cart/items`, {
                ProductID: id,
                Quantity: quantity
            });
        } catch (error) {
            console.error(':', error);
        }
    }, [authAxios]);

    // 
    useEffect(() => {
        return () => {
            // 
            Object.entries(pendingUpdates).forEach(([id, quantity]) => {
                updateQuantityImmediately(id, quantity);
            });
        };
    }, [pendingUpdates, updateQuantityImmediately]);

    return (
        <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
            <Box sx={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                mb: 2
            }}>
                <Typography variant="h4" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                    <CartIcon /> 
                </Typography>
                <Box sx={{ display: 'flex', gap: 2 }}>

                    {cartItems.length > 0 && (
                        <Button
                            variant="outlined"
                            color="error"
                            onClick={handleClearCart}
                            startIcon={<DeleteIcon />}
                        >
                            
                        </Button>
                    )}
                </Box>
            </Box>

            <Grid container spacing={3}>
                <Grid item xs={12} md={8}>
                    <Collapse in={showAlert}>
                        <Alert
                            severity={alertSeverity}
                            sx={{ mb: 2 }}
                            onClose={() => setShowAlert(false)}
                        >
                            {alertMessage}
                        </Alert>
                    </Collapse>

                    {cartItems.length > 0 ? (
                        cartItems.map((item) => (
                            <Fade in={true} key={item.id}>
                                <Card sx={{ mb: 2, position: 'relative' }}>
                                    <CardContent>
                                        <Grid container spacing={2} alignItems="center">
                                            <Grid item xs={12} sm={3}>
                                                <img
                                                    src={item.image}
                                                    alt={item.name}
                                                    onError={(e) => {
                                                        console.error(':', item.image?.substring(0, 50));
                                                        e.target.src = "https://via.placeholder.com/150";
                                                    }}
                                                    style={{
                                                        width: '100%',
                                                        height: 'auto',
                                                        borderRadius: '8px'
                                                    }}
                                                />
                                            </Grid>
                                            <Grid item xs={12} sm={9}>
                                                <Box sx={{
                                                    display: 'flex',
                                                    justifyContent: 'space-between',
                                                    alignItems: 'flex-start',
                                                    mb: 2
                                                }}>
                                                    <Typography variant="h6" component="div">
                                                        {item.name}
                                                    </Typography>
                                                    <IconButton
                                                        color="error"
                                                        onClick={() => removeItem(item.id)}
                                                        size="small"
                                                    >
                                                        <DeleteIcon />
                                                    </IconButton>
                                                </Box>
                                                <Box sx={{
                                                    display: 'flex',
                                                    justifyContent: 'space-between',
                                                    alignItems: 'center'
                                                }}>
                                                    <Box sx={{
                                                        display: 'flex',
                                                        alignItems: 'center',
                                                        gap: 1
                                                    }}>
                                                        <IconButton
                                                            size="small"
                                                            onClick={() => handleQuantityChange(item.id, item.quantity - 1)}
                                                            disabled={item.quantity <= 1}
                                                        >
                                                            <RemoveIcon />
                                                        </IconButton>
                                                        <TextField
                                                            type="number"
                                                            value={item.quantity}
                                                            onChange={(e) => handleQuantityChange(item.id, e.target.value)}
                                                            inputProps={{
                                                                min: 1,
                                                                max: 99,
                                                                step: 1
                                                            }}
                                                            sx={{ width: '80px' }}
                                                            size="small"
                                                            variant="outlined"
                                                            onBlur={() => {
                                                                // 
                                                                if (pendingUpdates[item.id]) {
                                                                    debouncedUpdateQuantity.flush(); // lodashdebounce
                                                                }
                                                            }}
                                                        />
                                                        <IconButton
                                                            size="small"
                                                            onClick={() => handleQuantityChange(item.id, item.quantity + 1)}
                                                            disabled={item.quantity >= item.stock}
                                                        >
                                                            <AddIcon />
                                                        </IconButton>
                                                    </Box>
                                                    <Typography variant="h6" color="primary">
                                                        NT$ {item.price * item.quantity}
                                                    </Typography>
                                                </Box>
                                                <Typography variant="caption" color="text.secondary">
                                                    : {item.stock} 
                                                </Typography>
                                                {pendingUpdates[item.id] && (
                                                    <Typography variant="caption" color="text.secondary">
                                                        ...
                                                    </Typography>
                                                )}
                                            </Grid>
                                        </Grid>
                                    </CardContent>
                                </Card>
                            </Fade>
                        ))
                    ) : (
                        <Paper sx={{ p: 3, textAlign: 'center' }}>
                            <Typography variant="h6" color="text.secondary">
                                
                            </Typography>
                            <Button
                                variant="contained"
                                sx={{ mt: 2 }}
                                onClick={() => navigate('/store/products')}
                            >
                                
                            </Button>
                        </Paper>
                    )}
                </Grid>

                <Grid item xs={12} md={4}>
                    <Paper sx={{ p: 3, position: 'sticky', top: 20 }}>
                        <Typography variant="h6" gutterBottom>
                            
                        </Typography>
                        <Box sx={{ my: 2 }}>
                            <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 1 }}>
                                <Typography></Typography>
                                <Typography>NT$ {total}</Typography>
                            </Box>
                            <Box sx={{
                                display: 'flex',
                                justifyContent: 'space-between',
                                mb: 1,
                                alignItems: 'center'
                            }}>
                                <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                                    <ShippingIcon fontSize="small" />
                                    <Typography></Typography>
                                </Box>
                                <Typography>
                                    {shippingFee === 0 ? '' : `NT$ ${shippingFee}`}
                                </Typography>
                            </Box>
                            {total < 2000 && (
                                <Typography variant="caption" color="primary">
                                     NT$ {2000 - total} 
                                </Typography>
                            )}
                        </Box>
                        <Divider sx={{ my: 2 }} />
                        <Box sx={{ display: 'flex', justifyContent: 'space-between', mb: 2 }}>
                            <Typography variant="h6"></Typography>
                            <Typography variant="h6" color="primary">
                                NT$ {total + shippingFee}
                            </Typography>
                        </Box>
                        <Button
                            variant="contained"
                            fullWidth
                            size="large"
                            onClick={handleCheckout}
                            disabled={cartItems.length === 0}
                        >
                            
                        </Button>
                    </Paper>
                </Grid>
            </Grid>
        </Container>
    );
};

export default Cart; 